import { FETCHED_FRIENDS, UPDATE_FRIENDS } from './actionType';
import {getFriends} from '../../utils/api/vk';

export const fetchedFriends = (setFetching) => async (dispatch) => {
	const data = await getFriends();
	dispatch({type: FETCHED_FRIENDS, users: data});
	if (setFetching) { setFetching(); }
};

export const updateFriends = (offset=0) => async (dispatch) => {
	const data = await getFriends(100, offset);
	dispatch({type: UPDATE_FRIENDS, users: [...data]});
};

export const fetchedBannedFriends = (setFetching) => {
	let offset = 0;
	const fetchedBanned = async (dispatch) => {
		const data = await getFriends(100, offset);

		if (!data.length && offset <= 5000) {
			offset += 100;
			fetchedBanned(dispatch);
		}

		dispatch({type: UPDATE_FRIENDS, users: [...data]});
		if (setFetching) { setFetching(); }
	};
	return fetchedBanned;
};